@media only screen and (max-width: 1700px) {
    html {
        font-size: 55%;
    }
}
@media only screen and (max-width: 1460px) {
    html {
        font-size: 48%;
    }
    .GameInit {
        top: 8rem;
        left: 20rem;
    }
}
@media only screen and (max-width: 1125px) {
    .GameInit {
        font-size: 3rem;
        top: 8rem;
        left: 20rem;
    }
}

@media only screen and (max-width: 830px) {
    * {
        cursor: default;
    }

    .TileContainer {
        top: 28rem;
        .item {
            cursor: default;
        }
    }
}

/* Small phones: from 0 to 480px */
@media only screen and (max-width: 400px) {
    * {
        cursor: default;
    }

    .GameInit {
        font-size: 16px;
        top: 15px;
        left:90px;
    }

    .TileContainer {
        font-size: 20px;
        width: 250px;
        height: 250px;
        top: 150px;
        .item {
            cursor: default;
        }
    }
}

